.region-header {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 0 1em;
  background: #37474f;
  color: #fff;
  box-shadow: 0 -5px 10px 5px rgba(0, 0, 0, 0.5);
  z-index: 10;
  min-height: 4em;
}
.region-header ul {
  list-style: none;
}
.region-header li.has-submenu ul {
  display: none;
}
.region-header li.has-submenu .submenu-icon {
  cursor: pointer;
  font-family: "Font Awesome 5 Free";
}
.region-header li.has-submenu .submenu-icon::before {
  display: inline-block;
  content: "";
  transform: rotate(0deg);
  transition: transform 0.2s ease;
}
.region-header li.has-submenu.open ul {
  display: block;
}
.region-header li.has-submenu.open .submenu-icon::before {
  transform: rotate(180deg);
}
.region-header-logo {
  flex: 0 0 auto;
  font-size: 2em;
  font-weight: 700;
  padding-right: 1em;
}
.region-header-logo a {
  color: #fff;
  text-decoration: none;
}
.region-header-nav {
  flex: 1;
}
.region-header-nav ul {
  display: flex;
  margin: 0;
  padding: 0;
}
.region-header-nav .menu {
  flex-wrap: wrap;
}
.region-header-nav li {
  display: inline-block;
  font-size: 1.2rem;
  padding: 0 0.3em;
  border-bottom: 3px solid transparent;
}
.region-header-nav li.selected, .region-header-nav li.selected-parent {
  border-bottom-color: #ea568c;
}
.region-header-nav li:hover {
  border-bottom-color: #d81b60;
}
.region-header-nav li a {
  display: inline-block;
  color: #fff;
  text-decoration: none;
  padding: 0 0.5em;
  height: 3em;
  line-height: 3;
}
.region-header-nav li.has-submenu {
  position: relative;
}
.region-header-nav li.has-submenu li {
  display: block;
  padding: 0;
}
.region-header-nav li.has-submenu li a {
  display: block;
  padding: 0 0.3em;
}
.region-header-nav li.has-submenu ul {
  position: absolute;
  top: 60px;
  left: 0;
  width: 100%;
  background: #37474f;
  padding: 0.2em 0.2em;
  box-shadow: 0 3px 10px 0px rgba(0, 0, 0, 0.5);
  z-index: 1;
}
.region-header-nav li.has-submenu:hover .submenu-icon::before {
  transform: rotate(180deg);
}
.region-header-nav li.has-submenu:hover > ul {
  display: flex;
  flex-direction: column;
}
@media screen and (max-width: 720px) {
  .region-header-nav {
    display: none;
  }
}
.region-header-mobile .menu-icon {
  display: none;
  font-family: "Font Awesome 5 Free";
  cursor: pointer;
  font-size: 2em;
  float: right;
  z-index: 3;
}
.region-header-mobile .menu-icon::before {
  content: "";
}
.region-header-mobile .submenu-icon {
  position: absolute;
  right: 0;
  padding: 0 1em;
}
.region-header-mobile .menu {
  display: block;
  position: fixed;
  right: 0;
  height: 100%;
  top: 0;
  z-index: 2;
  font-size: 1.5em;
  background: #37474f;
  color: #fff;
  width: 50%;
  max-width: 100vw;
  min-width: 12em;
  margin: 0;
  padding: 2em 1em 0;
  box-shadow: -3px 0px 10px 5px rgba(0, 0, 0, 0.5);
  transform: translateX(110%);
  transition: transform 0.2s ease;
}
.region-header-mobile li {
  margin: 0.3em 0;
}
.region-header-mobile li a {
  color: #fff;
  text-decoration: none;
  padding: 0.5em;
}
.region-header-mobile .submenu {
  padding-left: 1em;
}
.region-header-mobile .has-submenu li::before {
  content: "-";
}
@media screen and (max-width: 720px) {
  .region-header-mobile {
    flex: 1;
  }
  .region-header-mobile .menu-icon {
    display: block;
  }
  .region-header-mobile.open .menu-icon {
    position: absolute;
    top: 0.5rem;
    right: 1rem;
  }
  .region-header-mobile.open .menu-icon::before {
    content: "";
  }
  .region-header-mobile.open > ul {
    transform: translateX(0);
  }
}

footer {
  top: 0;
  width: 100%;
  background: #37474f;
  color: #fff;
  box-shadow: 0 5px 10px 5px rgba(0, 0, 0, 0.5);
  padding: 1em 2em;
}
footer a[href] {
  color: #fff;
}
footer a[href]:visited {
  color: #d81b60;
}
footer .columns {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  max-width: 800px;
  margin: 0 auto;
}
footer .columns .column {
  padding: 1em 0.5em;
  margin: 0 0.5em;
}
footer .columns .column:not(:last-of-type) {
  border-right: 1px solid #fff;
}
@media screen and (max-width: 720px) {
  footer .columns {
    grid-template-columns: 1fr;
  }
  footer .columns .column {
    border-bottom: 1px solid #fff;
    border-right: none !important;
    margin: 0;
  }
  footer .columns .column:last-of-type {
    border-bottom: none;
  }
}

/*
  Material like button
*/
button,
a.button {
  color: #d81b60;
  border: none;
  outline: none;
  padding: 0.6em 1.5em;
  margin: 0.3em 0.5em;
  border-radius: 0.2em;
  background: none;
  cursor: pointer;
  font-weight: 600;
  font-size: 1em;
  transition: all 0.2s ease;
  text-decoration: none;
}
button:disabled,
a.button:disabled {
  color: #888;
}
button:hover:not(:disabled),
a.button:hover:not(:disabled) {
  background: rgba(216, 27, 96, 0.2);
}
button.solid,
a.button.solid {
  color: #fff;
  background: #d81b60;
  box-shadow: 0 0.2em 0.4em 0 rgba(0, 0, 0, 0.4);
}
button.solid:disabled,
a.button.solid:disabled {
  background: #888;
}
button.solid:hover:not(:disabled),
a.button.solid:hover:not(:disabled) {
  background: #ab154c;
  box-shadow: 0 0.4em 0.4em 0 rgba(0, 0, 0, 0.4);
}

/*
  Material like textfield
*/
.textfield {
  position: relative;
  margin: 0.3em 0;
  min-width: 100px;
  width: 100%;
  max-width: 400px;
  color: #405863;
  height: 3.25em;
}
.textfield.fluid {
  max-width: 100%;
}
.textfield label {
  position: absolute;
  top: 1em;
  left: 1em;
  opacity: 0.5;
  font-weight: 500;
  transition: transform 0.2s ease, opacity 0.2s ease, color 0.2s ease;
  will-change: transform, opacity, color;
  transform-origin: top left;
  pointer-events: none;
  user-select: none;
}
.textfield input {
  background: #e6e6e6;
  font-size: 1em;
  padding: 1.5em 1em 0.5em;
  width: 100%;
  transition: border-color 0.2s ease, opacity 0.2s ease;
  border: none;
  border-bottom: 0.1em solid #8ea3ac;
  border-radius: 0.2em;
  outline: none;
  box-sizing: border-box;
  opacity: 0.5;
}
.textfield input:hover {
  opacity: 0.8;
}
.textfield input:focus {
  border-color: #d81b60;
  opacity: 1;
}
.textfield input:focus + label, .textfield input:not(:placeholder-shown) + label {
  color: #d81b60;
  transform: translateY(-60%) scale(0.9);
  opacity: 1;
}
.textfield input:required + label::after {
  content: "*";
}
.textfield input::-webkit-outer-spin-button, .textfield input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.textfield input[type=number] {
  -moz-appearance: textfield;
}

#query {
  display: inline;
}

.table {
  max-width: 100%;
  overflow-x: auto;
  margin-bottom: 2em;
}
.table table {
  width: 100%;
  border-spacing: 0;
  border-collapse: collapse;
  text-align: left;
}
.table thead {
  font-weight: bold;
  color: #fff;
  background: #37474f;
  border: 1px solid #37474f;
}
.table tbody {
  color: #606060;
  border: 1px solid #e0e0e0;
}
.table tbody tr:nth-child(even) {
  background: rgba(216, 27, 96, 0.1);
}
.table td,
.table th {
  padding: 0.8em 0.5em 0.8em 2.5em;
  text-align: left;
}
.table td:last-of-type,
.table th:last-of-type {
  padding-right: 2.5em;
}
.table td.right,
.table th.right {
  text-align: right;
}
.table td a {
  text-decoration: none;
}
.table tr.clickable {
  cursor: pointer;
}

select {
  height: 2.5rem;
}

label {
  display: block;
  font-weight: bold;
}

form {
  padding: 0 1em;
}
@media screen and (max-width: 720px) {
  form input {
    width: 100%;
  }
}

main {
  min-height: 500px;
}
main.has-sidebar-right {
  display: grid;
  grid-template-columns: auto 250px;
  grid-gap: 1em;
}
main.has-sidebar-left {
  display: grid;
  grid-template-columns: 250px auto;
  grid-gap: 1em;
}
main.has-sidebar-left.has-sidebar-right {
  grid-template-columns: 250px auto 250px;
}
@media screen and (max-width: 720px) {
  main.has-sidebar-right, main.has-sidebar-left {
    grid-template-columns: 1fr;
  }
}

.region-sidebar {
  position: sticky;
  top: 1em;
}
.region-sidebar .wrapper {
  position: sticky;
  top: 1em;
}
.region-sidebar h4 {
  background: #37474f;
  color: #fff;
  padding: 0.2em 1em;
}

.breadcrumb-list ul {
  display: flex;
}
.breadcrumb-list li {
  display: inline;
}
.breadcrumb-list li::after {
  content: ">";
  margin: 0 0.5em;
}

.next-previous {
  display: flex;
}
.next-previous .previous {
  flex: 1;
  text-align: left;
  order: 1;
}
.next-previous .next {
  flex: 1;
  text-align: right;
  order: 2;
}

/**
 * Dice styles
 */
.dice {
  width: 3em;
  height: 3em;
  margin: 0 0.5em;
  color: rgba(255, 0, 0, 0.8);
  background: #fff;
}

.players {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-column-gap: 1rem;
  grid-row-gap: 1rem;
  margin-bottom: 2em;
}
@media screen and (max-width: 720px) {
  .players {
    grid-template-columns: 1fr;
  }
}

/* Message */
.message {
  color: #fff;
  background: #000;
  margin: 1.5em 1em 1.5em;
  padding: 0.8em 1em;
  border: 2px solid #000;
  font-size: 1em;
  border-radius: 0.3em;
}
.message header {
  font-size: 1.1em;
  margin-bottom: 0.5em;
  font-weight: bold;
}
.message.info {
  color: #276f86;
  background: #f8ffff;
  border-color: #a9d5de;
}
.message.ok {
  color: #2c662d;
  background: #fcfff5;
  border-color: #a3c293;
}
.message.warning {
  color: #573a08;
  background: #fffaf3;
  border-color: #c9ba9b;
}
.message.error {
  color: #9f3a38;
  background: #fff6f6;
  border-color: #e0b4b4;
}